        { value: 'd', text: 'This one is disabled', disabled: true }
      ],
      languages:localStorage.getItem('lang')
    }
  },
  mounted() {
    this.$store.dispatch('newsM',1)
    this.languages=localStorage.getItem('lang')
  },
  methods: {
    clickCallback (pageNum){

